<template>
	<view class="board">
		<view class="box">
			<view class="title">项目列表</view>
			<searchBox :showEmployee="true" @handleSearch="handleSearch">
			</searchBox>
			<view class="table_wrap">
				<view class="tabs flex flex_start">
					<view class="tab" v-for="(item,index) in tabs" :class="currentTab.value==item.value?'active':''"
						@click="changeTab(item)">{{item.text}}</view>
				</view>
				<uni-table border stripe emptyText="暂无更多数据" style="height:500px;">
					<uni-tr style="background: #f6f6f6;">
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">工人姓名</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">身份证号</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">手机号</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">性别</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">年龄</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">状态</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">注册时间</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">操作</uni-th>
					</uni-tr>
					<uni-tr v-for="(item,index) in list" :key="index">
						<uni-td align="center">
							<view style="cursor: pointer;color:#1E8EEE;" @click="showWorkerDetail(item)">
								{{item.name}}
							</view>
						</uni-td>
						<uni-td align="center">{{item.id_card_number}}</uni-td>
						<uni-td align="center">{{item.mobile}}</uni-td>
						<uni-td align="center">{{item.gender == "male"?"男":"女"}}</uni-td>
						<uni-td align="center">{{item.age}}</uni-td>
						<uni-td align="center">
							<view class="status">{{item.status == "normal"?"正常":"已冻结"}}</view>
						</uni-td>
						<uni-td align="center">{{item.create_time}}</uni-td>
						<uni-td align="center" style="padding:8px 0px;" v-if="currentTab.value!=2">
							<view class="ope flex flex_around">
								<view class="ope_item" @click="handleAccount(item)">{{item.status=="normal"?"冻结":"解冻"}}
								</view>
								<view class="ope_item" @click="showWorkerDetail(item)">详情</view>
							</view>
						</uni-td>
					</uni-tr>

				</uni-table>
				<pageBox :page="pagination" @toNext="toNext"></pageBox>
			</view>
		</view>
		<view class="mask" v-if="showMask2" style="z-index:101;">
			<view class="inner" v-if="showFirst">
				<view class="title" style="margin-bottom: 0;">身份证预览</view>
				<view class="close" @click="closeId">
					<image src="/static/close_white.png" mode="widthFix"></image>
				</view>
				<view class="box flex flex_btween">
					<view class="img" style="width:46%;">
						<image :src="currentWorkerInfo.id_card_front_image" mode="widthFix" style="width:100%;"></image>
					</view>
					<view class="img" style="width:46%;">
						<image :src="currentWorkerInfo.id_card_back_image" mode="widthFix" style="width:100%;"></image>
					</view>
				</view>
			</view>
		</view>
		<view class="mask" v-if="showMask" style="z-index:100;">
			<view class="inner" v-if="showThird">
				<view class="title" style="margin-bottom: 0;">工人详情</view>
				<view class="close" @click="close">
					<image src="/static/close_white.png" mode="widthFix"></image>
				</view>
				<view class="box">
					<view class="base">
						<view class="out_line flex flex_start">
							<view class="line flex flex_start">
								<view class="tit">工人姓名：</view>
								<view class="text">{{currentWorkerInfo.name}}</view>
							</view>
							<view class="line flex flex-start">
								<view class="tit">工人性别：</view>
								<view class="text">{{currentWorkerInfo.gender=="male"?"男":"女"}}</view>
							</view>

						</view>
						<view class="out_line flex flex_start">
							<view class="line flex flex-start">
								<view class="tit">身份证号：</view>
								<view class="text">{{currentWorkerInfo.id_card_number}}</view>
							</view>
							<view class="line flex flex-start">
								<view class="tit">年龄：</view>
								<view class="text">{{currentWorkerInfo.age}}</view>
							</view>
						</view>
						<view class="out_line flex flex_start">
							<view class="line flex flex-start">
								<view class="tit">手机号码：</view>
								<view class="text">{{currentWorkerInfo.mobile}}</view>
							</view>
							<view class="line flex flex-start">
								<view class="tit">身份证：</view>
								<view class="text" style="cursor: pointer;" @click="showIdCard">
									<image src="/static/id_card_icon.png" mode="widthFix"
										style="width:40px;height:40px;"></image>
								</view>
							</view>
						</view>
						<view class="out_line flex flex_start">
							<view class="line flex flex-start">
								<view class="tit">状态：</view>
								<view class="text">{{currentWorkerInfo.status == "normal"?"正常":"已冻结"}}</view>
							</view>
							<view class="line flex flex-start">
								<view class="tit">注册时间：</view>
								<view class="text">{{currentWorkerInfo.create_time}}</view>
							</view>
						</view>
					</view>
					<view class="tabs flex flex_start">
						<view class="tab active">履职记录</view>
					</view>
					<view class="table_wrap">
						<uni-table border stripe emptyText="暂无更多数据" style="height:500px;">
							<uni-tr style="background: #f6f6f6;">
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">项目名称</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">归属公司</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">报名时间</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">入职时间</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">服务地址</uni-th>
								<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">状态</uni-th>
							</uni-tr>
							<uni-tr v-for="(item,index) in workerProjectRecordList" :key="index">
								<uni-td align="center">{{item.project_name}}</uni-td>
								<uni-td align="center">{{item.broker_company_name}}</uni-td>
								<uni-td align="center">{{item.create_time}}</uni-td>
								<uni-td align="center">{{item.entry_time}}</uni-td>
								<uni-td align="center">{{item.work_address}}</uni-td>
								<uni-td align="center">
									<view class="status">
										{{status.filter(el => {return el.value == item.status})[0].text}}
									</view>
								</uni-td>
							</uni-tr>
						</uni-table>
						<pageBox :page="maskPagination" @toMaskNext="toMaskNext" :type="pageType"></pageBox>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import searchBox from "@/components/search_box.vue"
	import pageBox from "@/components/paging_box.vue"
	import commonData from "@/common/commonData.js"
	export default {
		name: "employee_list",
		data() {
			return {
				timeStr: "报名",
				pageType: "mask",
				list: [],
				tabs: [{
						value: "all",
						text: "全部"
					},
					{
						value: "normal",
						text: "正常"
					},
					{
						value: "freeze",
						text: "冻结"
					}
				],
				showMask2: false,
				status: commonData.workerStatus,
				pagination: {},
				showMask: false,
				showFirst: false,
				showSecond: false,
				showThird: false,
				seleAll: false,
				selectedIds: [],
				selectedIndex: [],
				currentTab: {
					value: "all",
					text: "全部"
				},
				currentPage: 1,
				id: "",
				currentInfo: {
					"broker_mobile": "",
					"broker_name": "",
					"code": "",
					"content": "",
					"id": "",
					"name": "",
					"work_address": "",
					"worker_salary": "",
					"worker_welfare": ""
				},
				waitDepartList: [], //待离职名单
				searEmployee: "",
				searStart: "",
				searEnd: "",
				currentWorker: {},
				currentWorkerInfo: {},
				workerCheckinList: [],
				workerProjectRecordList: [],
				workerSalaryList: [],
				currentSubTab: 0,
				maskPageIndex: 1,
				maskPagination: {},
				checkStatus: [{
						value: "to_be_settled",
						text: "待结算"
					},
					{
						value: "to_be_issued",
						text: "待发放"
					},
					{
						value: "issued",
						text: "已发放"
					},
					{
						value: "voided",
						text: "已作废"
					},
				],
				salaryHandlePeriod: commonData.paySalaryPeriod,
				period: commonData.periodList,
				showMoreInfo: false,
				moreText: "展开全部",
				currentWorkerId: ""
			};
		},
		components: {
			searchBox,
			pageBox
		},
		created() {
			let paramArr = []
			if (location.href.indexOf("?") != -1) {
				paramArr = location.href.split("?")[1].split("&")
			}
			for (var i = 0; i < paramArr.length; i++) {
				if (paramArr[i].indexOf("id") != -1) {
					this.id = paramArr[i].split("=")[1]
				}
			}
			this.getList()
		},
		methods: {
			toNext(e) {
				this.currentPage = e
				this.getList()
			},
			closeId() {
				this.showFirst = false
				this.showMask2 = false
			},
			showIdCard() {
				this.showMask2 = true
				this.showFirst = true
			},
			toMaskNext(e) {
				this.maskPageIndex = e
				this.getWorkerProjectRecord()
			},
			showWorkerDetail(item) {
				this.currentWorkerId = item.id
				let url = "/admin/worker/" + item.id + "/info"
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.currentWorkerInfo = res.data
						this.showMask = true
						this.showThird = true
						this.getWorkerProjectRecord()
					}
				})
			},
			getWorkerProjectRecord() {
				let url = "/admin/worker/" + this.currentWorkerId + "/sign-request?page=" + this.maskPageIndex
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.workerProjectRecordList = res.data.list
						this.maskPagination = res.data.pagination
					}
				})
			},
			handleAccount(item) {
				let _this = this
				let url = "/admin/worker/freeze-or-thawing"
				let data = {}
				if (item.status == "normal") {
					data = {
						"id": item.account_id, // 雇工账号Id
						"freeze": true // true 冻结 fasle 解冻
					}
					uni.showModal({
						title: "冻结",
						content: "是否确认冻结账户：" + item.name + "？",
						success(resp) {
							if (resp.confirm) {
								_this.$request(url, data, "POST").then(res => {
									if (res.code == 0) {
										uni.showToast({
											title: "该账户已冻结",
											icon: "none",
											duration: 3000
										})
										_this.getList()
									}
								})
							}
						}
					})
				} else {
					data = {
						"id": item.account_id, // 雇工账号Id
						"freeze": false // true 冻结 fasle 解冻
					}
					uni.showModal({
						title: "解冻",
						content: "是否确认解冻账户：" + item.name + "？",
						success(resp) {
							if (resp.confirm) {
								_this.$request(url, data, "POST").then(res => {
									if (res.code == 0) {
										uni.showToast({
											title: "该账户已解冻",
											icon: "none",
											duration: 3000
										})
										_this.getList()
									}
								})
							}
						}
					})
				}
			},
			changeTab(item) {
				this.currentTab.value = item.value
				this.currentTab.text = item.text
				this.getList()
			},
			getList() {
				let url = "/admin/worker/" + this.currentTab.value + "?page=" + this.currentPage +
					"&keyword=" + this.searEmployee
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.list = res.data.list
						this.pagination = res.data.pagination
					}
				})
			},
			handleSearch(e) {
				this.searEmployee = e.employeeName
				this.searStart = e.startTime
				this.searEnd = e.endTime
				this.getList()
			},
			close() {
				this.showMask = false
				this.showFirst = false
				this.showSecond = false
				this.showThird = false
				this.currentWorkerId = ""
			},
		}
	}
</script>

<style lang="scss">
	::v-deep {
		.search_box {
			margin: 0;
		}
	}

	.active {
		color: #0da81f;
	}

	.disable {
		color: #f00;
	}

	.disable {
		color: #f00;
	}

	.ope {
		position: relative;
		padding: 0 10px;
		width: 100%;
		box-sizing: border-box;

		.more {
			position: absolute;
			top: calc(100% + 8px);
			left: 0;
			width: 100%;
			z-index: 9;
			background: #fff;
			border-radius: 5px;
			box-shadow: 2px 2px 5px 3px #ddd;
			padding: 5px 0;

			.opera {
				text-align: center;
				height: 30px;
				line-height: 30px;
				cursor: pointer;
			}
		}
	}

	.showmore {
		width: 100%;
		text-align: center;
		margin: 15px 0;
		cursor: pointer;

		view {
			display: inline-block;
			color: #0F90FF;
		}

		image {
			width: 20px;
			vertical-align: middle;
		}
	}

	.base {
		padding: 0 0 10px 0;
		// height: 100px;
		// overflow: hidden;

		&.showall {
			height: auto;
			overflow: visible;
		}

		.out_line {
			.line {
				width: 40%;
				margin-top: 0;

				.tit {
					width: 100px;
				}
			}
		}

		.line {
			margin: 15px 0;

			.text {
				color: #666;
				font-size: 14px;
			}
		}
	}

	.table_wrap {

		// height:500px;
		// .tabs{
		// 	margin: 15px 0;
		// 	border-bottom: 2px solid $base-color;
		// 	.tab{
		// 		padding:10px 15px;
		// 		cursor: pointer;	
		// 		&:hover{
		// 			color:$base-color;
		// 		}
		// 		&.active{
		// 			background: $base-color;
		// 			color:#fff;
		// 			border-top-left-radius: 4px;
		// 			border-top-right-radius: 4px;
		// 		}
		// 	}
		// }
		.btns {
			margin-bottom: 15px;

			.btn {
				margin-left: 15px;
			}
		}

		.table_top {
			margin-bottom: 15px;

			.btn {
				width: 100px;
				margin-left: 15px;
			}
		}

		.tips {
			margin-bottom: 20px;

			view {
				margin-right: 15px;
				font-size: 14px;

				text {
					color: #0092ff;
				}
			}

			.tip {
				color: #f00;
			}
		}
	}

	.box {
		.box_item {
			&:nth-child(2) {
				padding-left: 20px;

				.title {
					height: 40px;
					line-height: 40px;
					margin: 0;
					padding-left: 0;

					&:before {
						display: none;
					}
				}
			}

			.l_tabs {
				.l_tab {
					padding-left: 15px;
					width: 80px;
					height: 40px;
					line-height: 40px;

					&:hover {
						color: #1890FF;
						cursor: pointer;
					}

					&.active {
						background: #E7F7FF;
						color: #1890FF;
						border-right: 3px solid #1890FF;
					}

					.mark {
						display: inline-block;
						margin-left: 10px;
						color: #f00;
					}
				}
			}
		}

		.box_right {
			width: calc(100% - 80px);
		}
	}

	.mask {
		.inner {
			.title {
				position: relative;
				margin-bottom: 30px;
				font-weight: 600;

				text {
					position: absolute;
					font-size: 14px;
					top: 50%;
					right: 0;
					transform: translateY(-50%);
					color: $base-color;
				}
			}

			.tabs {
				margin-bottom: 15px;

				.tab {
					padding: 5px 10px;
					border: 1px solid #ccc;
					margin-right: 15px;
					border-radius: 4px;
					cursor: pointer;

					&.active {
						background: $base-color;
						color: #fff;
						border: 1px solid $base-color;
					}
				}
			}

			.setting_box {
				width: 100%;
				height: 100%;
				background: rgba(0, 0, 0, 0.5);
				position: absolute;
				top: 0;
				left: 0;
				z-index: 100;

				.set_inner {
					width: 230px;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					z-index: 100;
					background: #fff;
					padding: 20px;
					border-radius: 6px;

					.subtit {
						font-size: 18px;
					}

					.line {
						padding-left: 10px;
						margin: 20px 0;

						.tit {
							width: 70px;
						}

						.input_wrap {
							width: calc(100% - 70px);
							height: 40px;
							line-height: 40px;
							border: 1px solid #eee;

							input {
								height: 40px;
								padding-left: 10px;
							}
						}
					}
				}

			}
		}
	}
</style>